import React from 'react'
import SkillSets from './SkillSets'

const ProjectCard = (props) => {
    const skillSets = props.project.skills.map((skill,index) => 
        <SkillSets skill={skill} key={index} />    
    )
    return (
        <div className='projCard'>
            <img src={props.project.pictures[0]} alt="" />
            <div className="projTitle">{props.project.title}</div>
            <div className="projSkills">
                {skillSets}
            </div>
        </div>
    )
}

export default ProjectCard