import React, { useContext } from 'react'
import './navbar.scss'
import { DarkModeContext } from '../../Contexts/DarkModeContext'

import LightModeOutlinedIcon from '@mui/icons-material/LightModeOutlined';
import DarkModeOutlinedIcon from '@mui/icons-material/DarkModeOutlined';
import { Link } from 'react-router-dom';

function Navbar() {
    const {darkMode, toggle} = useContext(DarkModeContext)
    return (
        <div className='navbar'>
            <div className="logo"><Link to="/anand-github">ANAND</Link></div>
            <div className="quickLinks">
                <span><Link to="/anand-github/projects">Projects</Link></span>
                <span><Link to="/anand-github/about">About Me</Link></span>
            </div>
            {darkMode ? <LightModeOutlinedIcon onClick={toggle} /> :<DarkModeOutlinedIcon onClick={toggle} />}
        </div>
    )
}

export default Navbar